.stats-grid {
  display: grid;
  grid-template-colums: 1fr 1fr 1fr; }
  .stats-grid .top-panel {
    grid-row-start: 1;
    grid-column-start: 2;
    grid-column-end: 3; }
  .stats-grid .bottom-panel {
    grid-row-start: 2;
    grid-column-start: 1;
    grid-column-end: 3; }
  .stats-grid .switch-selector span {
    color: gray; }
  .stats-grid .switch-selector span.active {
    color: black; }

.box-title {
  text-align: center; }

.reset-button {
  cursor: pointer; }

.chart-wrapper {
  display: inline-block; }
