rect.disabled {
  opacity: 0.2; }

.chart {
  display: inline-block;
  width: 30vw; }
  .chart .clickable-path {
    cursor: pointer; }
  .chart .legend rect, .chart .legend text {
    cursor: pointer; }

.tooltip {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  background-color: white;
  padding: 5px;
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 1px #8080809c;
  transition: 0.5s visibility, 0.5s opacity; }
  .tooltip.active {
    visibility: visible;
    opacity: 1; }
