ul#analysis-list li.more-results-btn,
ul#analysis-list button.more-results-btn {
  background: #9ed4c7;
  background: -moz-linear-gradient(top, #9ed4c7 0%, #73ac78 100%);
  background: -webkit-linear-gradient(top, #9ed4c7 0%, #73ac78 100%);
  background: linear-gradient(to bottom, #9ed4c7 0%, #73ac78 100%);
  text-transform: uppercase;
  color: #f8f8ff;
  font-size: 150%;
  min-height: 2em;
  max-height: 2em;
  text-align: center;
  line-height: 2em;
  -webkit-user-select: none;
  /* Chrome all / Safari all */
  -moz-user-select: none;
  /* Firefox all */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none;
  /* Likely future */
  cursor: pointer; }

.main-content {
  padding: 4rem 0 0 0; }
  @media screen and (min-width: 550px) {
    .main-content {
      padding: 1rem 0 0 0; } }

@keyframes card-animation {
  0% {
    opacity: 0;
    transform: scale(0.987) translateY(-10px); }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0); } }

@keyframes marks-animation {
  0% {
    opacity: 0;
    background-color: transparent;
    border-color: transparent;
    transform: scaleX(0.95); }
  20% {
    opacity: 0.7;
    background-color: transparent;
    border-color: transparent;
    transform: scaleX(0.95); }
  100% {
    opacity: 1;
    width: scaleX(1); } }

.listening-spinner {
  width: 1rem;
  height: 1rem;
  display: inline-block;
  vertical-align: baseline;
  background-color: #333;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out; }

div.listening {
  color: #3f2a72;
  text-align: center;
  height: 2rem;
  line-height: 2rem;
  min-height: 1rem;
  font-size: 1.1rem;
  margin-top: 4rem; }
  @media screen and (min-width: 550px) {
    div.listening {
      margin-top: 1rem;
      margin-bottom: 2rem; } }

@-webkit-keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
    opacity: 0; } }

@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0; } }

.user-tag {
  font-family: 'raleway-bold'; }
  .user-tag.news-source {
    color: #001f3f; }
  .user-tag.doctor {
    color: #ff851b; }
  .user-tag.generic {
    color: #0074d9; }
  .user-tag.health-initiative, .user-tag.healthcare-initiative {
    color: #ff4136; }
  .user-tag.interested-in.healthcare {
    color: #39cccc; }
  .user-tag.professional, .user-tag.specialist {
    color: #85144b; }
  .user-tag.academia {
    color: #f012be; }
  .user-tag.med-business {
    color: #3d9970; }
  .user-tag.institution {
    color: #2ecc40; }
  .user-tag.patient {
    color: #777777; }
  .user-tag.publishing-source {
    color: #ffdc00; }
  .user-tag.association {
    color: #777777; }
  .user-tag.pubmed-tag {
    color: #ab47bc; }
  .user-tag.clinicaltrials-tag {
    color: #4abca8; }

button.add-analysis,
button.show-help {
  display: block;
  cursor: pointer;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  border-radius: 100px;
  outline: none;
  background-color: #73ac78;
  color: #f8f8ff;
  font-size: 200%;
  padding: 0;
  width: 50px;
  height: 50px;
  border: none;
  box-shadow: 1px 1px 5px gray; }

button.show-help {
  right: calc(3rem + 50px); }

#help-window {
  cursor: pointer;
  position: fixed;
  bottom: calc(3rem + 50px);
  right: 2rem;
  width: calc(100% - 6rem);
  max-width: 40rem;
  padding: 1rem;
  background-color: white;
  visibility: hidden;
  height: auto;
  max-height: 0;
  opacity: 0;
  overflow-y: auto;
  transition: visibility 0.5s, opacity 0.5s, max-height 1s;
  box-shadow: 0px 0px 10px 0px #c1c1c1; }
  #help-window.active {
    max-height: calc(100% - 100px - 7rem);
    visibility: visible;
    opacity: 1; }
  #help-window ul {
    padding: 0 0 0 1rem; }

ul#analysis-list {
  list-style: none;
  max-width: 450px;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 110px;
  padding: 0;
  border-radius: 3px;
  box-shadow: 0px 0px 10px 0px #e7e7e7; }
  ul#analysis-list li,
  ul#analysis-list button {
    background: #ffffff;
    background: -moz-linear-gradient(top, #ffffff 0%, #f8f8ff 100%);
    background: -webkit-linear-gradient(top, #ffffff 0%, #f8f8ff 100%);
    background: linear-gradient(to bottom, #ffffff 0%, #f8f8ff 100%);
    overflow: hidden;
    padding: 0.8rem;
    border-top: 1px solid #ebebeb;
    min-height: 5em;
    border-radius: 3px;
    vertical-align: middle;
    font-size: 90%; }
    ul#analysis-list li.analysis-item,
    ul#analysis-list button.analysis-item {
      animation: card-animation 0.5s 1; }
    ul#analysis-list li span,
    ul#analysis-list li div,
    ul#analysis-list button span,
    ul#analysis-list button div {
