        background-image: url("../../../assets/images/twitter-icon.png"); }
      ul#analysis-list li i.source.pubmed,
      ul#analysis-list button i.source.pubmed {
        background-image: url("../../../assets/images/pubmed-icon.png"); }
      ul#analysis-list li i.source.clinicaltrials,
