#analysis-form .send-button {
  background: #9ed4c7;
  background: -moz-linear-gradient(top, #9ed4c7 0%, #73ac78 100%);
  background: -webkit-linear-gradient(top, #9ed4c7 0%, #73ac78 100%);
  background: linear-gradient(to bottom, #9ed4c7 0%, #73ac78 100%);
  text-transform: uppercase;
  color: #f8f8ff;
  font-size: 150%;
  min-height: 2em;
  max-height: 2em;
  text-align: center;
  line-height: 2em;
  -webkit-user-select: none;
  /* Chrome all / Safari all */
  -moz-user-select: none;
  /* Firefox all */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none;
  /* Likely future */
  cursor: pointer; }

.main-content {
  padding: 4rem 0 0 0; }
  @media screen and (min-width: 550px) {
    .main-content {
      padding: 1rem 0 0 0; } }

.close-button {
  cursor: pointer;
  position: absolute;
  background-image: url("../../../assets/images/close-icon.svg");
  top: 1.5rem;
  right: 1.6rem;
  height: 1.6rem;
  width: 1.6rem;
  background-size: contain;
  background-color: transparent;
  border: none; }

#form-mask {
  z-index: 15;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: visibility 0.5s, background-color 0.5s; }
  #form-mask.active {
    background-color: rgba(0, 0, 0, 0.5);
    visibility: visible; }

.active #analysis-form {
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
  opacity: 1; }

#analysis-form {
  position: fixed;
  visibility: hidden;
  overflow: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s, transform 0.5s;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.9);
  padding: 0;
  height: 80vh;
  max-width: 470px;
  width: 80vw;
  background-color: white; }
  #analysis-form .close-form {
    position: fixed;
    text-align: center;
    line-height: 1.3rem;
    top: 0.5rem;
    right: 0.5rem;
    font-size: 2rem;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 2rem;
    color: white;
    background-color: #c1c1c1;
    cursor: pointer; }
  #analysis-form .form-content {
    padding: 0rem 2rem 2rem 2rem;
    margin-top: 2rem;
    height: calc(100% - 5.5rem);
    overflow-y: auto; }
  #analysis-form h2 {
    height: 1rem;
    text-align: center; }
  #analysis-form .send-button {
    font-family: 'raleway-light';
    border: none; }
  #analysis-form form {
    display: flex;
    flex-flow: column; }
    #analysis-form form input:focus,
    #analysis-form form textarea:focus {
      outline-color: #73ac78; }
    #analysis-form form .input-section {
      width: 100%;
      margin-right: 0.5rem; }
    #analysis-form form .row {
      display: flex;
      flex-flow: row wrap;
      margin: 0.5rem 0 0.5rem 0; }
      #analysis-form form .row label {
        flex: 1;
        line-height: 2rem; }
      #analysis-form form .row input {
        flex: 2;
        width: calc(100% - 0.3rem);
        padding-left: 0.5rem;
        font-size: 150%;
        border: 1px solid gray;
        border-radius: 3px;
        transition: outline 1s;
        margin-right: 0.5rem; }
      #analysis-form form .row .error-message {
        color: #fa0000;
        text-transform: uppercase;
        font-size: 75%;
        padding-top: 0;
        width: 100%;
        text-align: right;
        display: inline-block;
        line-height: 1rem; }
    #analysis-form form label.message {
      margin: 0.5rem 0 0.5rem 0; }
    #analysis-form form textarea {
      width: 100%;
      border: 1px solid gray;
      border-radius: 3px;
      transition: outline 1s;
      padding-right: 0.2rem; }
  #analysis-form .info {
    width: calc(100% - 0.2rem);
    padding-right: 0.3rem;
    font-size: 85%;
    border-bottom: 3px double gray; }
