#about-content {
  animation: fade-in 0.5s 1;
  text-align: justify;
  margin: auto;
  max-width: 500px;
  margin-top: 50px;
  padding: 1rem 1rem 0 1rem; }
  @media screen and (min-width: 550px) {
    #about-content {
      padding: 0; } }
  #about-content footer {
    height: 70px; }
  #about-content .author-info {
    margin-bottom: 2rem; }
  #about-content .left-photo,
  #about-content .right-photo {
    width: 7rem; }
  #about-content .left-photo {
    float: left;
    margin-right: 1rem; }
  #about-content .right-photo {
    float: right;
    margin-left: 1rem; }
