@keyframes bg-rotate {
  0% {
    transform: rotate(0deg) scale(1); }
  25% {
    transform: rotate(3deg) scale(1.1); }
  50% {
    transform: rotate(-3deg) scale(1.2); }
  100% {
    transform: rotate(0deg) scale(1); } }

#img-loader {
  z-index: -5;
  position: fixed;
  top: -10px;
  left: -10px;
  width: 1px;
  height: 1px;
  overflow: hidden; }
  #img-loader .l-1 {
    background-image: url(../../../assets/images/bg-1.jpg); }
  #img-loader .l-2 {
    background-image: url(../../../assets/images/bg-2.jpg); }
  #img-loader .l-3 {
    background-image: url(../../../assets/images/bg-3.jpg); }
  #img-loader .l-4 {
    background-image: url(../../../assets/images/bg-4.jpg); }
  #img-loader .l-5 {
    background-image: url(../../../assets/images/bg-5.jpg); }

#home-content {
  animation: fade-in 0.5s 1;
  max-width: 900px;
  margin: auto; }
  #home-content div.background {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    background-size: cover;
    opacity: 0.2;
    animation: bg-rotate 25s infinite;
    transition: background-color 1s, background-image 1s, background-position 10s;
    background-position: center; }
    #home-content div.background.slide-1 {
      background-image: url(../../../assets/images/bg-1.jpg); }
    #home-content div.background.slide-2 {
      background-image: url(../../../assets/images/bg-2.jpg); }
    #home-content div.background.slide-3 {
      background-image: url(../../../assets/images/bg-3.jpg); }
    #home-content div.background.slide-4 {
      background-image: url(../../../assets/images/bg-4.jpg); }
    #home-content div.background.slide-5 {
      background-image: url(../../../assets/images/bg-5.jpg); }
  #home-content section {
    display: table;
    position: relative;
    width: 100%;
    min-height: 55vh; }
    #home-content section span.smaller {
      font-size: 70%; }
    #home-content section p {
      color: #170f29;
      padding: 2rem;
      display: table-cell;
      vertical-align: middle;
      width: 80%;
      opacity: 0.1;
      transform: scale(0.9);
      transition: transform 5s, opacity 1s; }
      @media screen and (orientation: portrait) {
        #home-content section p {
          font-size: 9vw; } }
      @media screen and (orientation: landscape) {
        #home-content section p {
          font-size: 5vw; } }
      #home-content section p.p-right {
        text-align: right; }
      #home-content section p.p-left {
        float: left; }
    #home-content section.active p {
      opacity: 1; }
    #home-content section.active p.p-left {
      transform: scale(1) translateX(1rem); }
    #home-content section.active p.p-right {
      transform: scale(1) translateX(-1rem); }
  #home-content #section-1 {
    min-height: 63vh; }
  #home-content #section-5 {
    height: calc(78vh - 5rem);
    padding-bottom: 22vh;
